<template>
  <div class="characteristics">
    <div class="characteristics__item">
      <img class="characteristics__img" :src="`images/${image}`" alt="" />
      <div class="characteristics__btn-box">
        <button
          class="characteristics__switch-btn"
          v-for="(variant, index) in variants"
          :key="variant.variantId"
          :style="{ backgroundColor: variant.variantColor }"
          @click="updateProduct(index)"
        ></button>
        <p class="characteristics__btn-title">{{ title }}</p>
      </div>
      <p class="characteristics__price">{{ price.toLocaleString() }} ‚ÇΩ</p>
      <div class="characteristics__buy-box">
        <button class="characteristics__btn">–ø–æ—Å–º–æ—Ç—Ä–µ—Ç—å –Ω–∞–ª–∏—á–∏–µ</button>
        <a class="characteristics__link">–≤ –∫—Ä–µ–¥–∏—Ç –æ—Ç {{ credit.toFixed().toLocaleString() }} ‚ÇΩ/–º–µ—Å—è—Ü</a>
      </div>
    </div>
    <div class="characteristics__item">
      —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏—á–Ω—ã–µ —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫–∏, –ø—Ä—è–º —Å—É–ø–µ—Ä –ø–æ–¥—Ä–æ–±–Ω—ã–µ –≤–æ—Ç —á—Ç–æ –±—ã —Ç—É–ø—ã—Ö –≤–æ–ø—Ä–æ—Å–æ–≤ —Å–æ–≤—Å–µ–º –Ω–µ –æ—Å—Ç–∞–ª–æ—Å—å. –Ω—É –Ω–∏ –∫–∞–ø–µ–ª—é—à–µ—á–∫–∏. –≤–æ—Ç –ø—Ä—è–º–æ –ø–æ–¥—É–º–∞–ª –∫–∞–∫–æ–µ –ø–ª–µ—Ç–µ–Ω–∏–µ —Ç–∫–∞–Ω–∏ –Ω–∞ —Å–∏–¥—É—Ö–µ –∏ —Ç—É—Ç –¥–∞–∂–µ —ç—Ç–æ –µ—Å—Ç—å.
    </div>
  </div>
</template>

<script>
export default {
  name: "CharacteristicsApp",
  data: function () {
    return {
      selectedVariant: 0,
      variants: [
        {
          variantImage: "main.jpg",
          variantPrice: 100000,
          variantColor: "red",
          variantTitle: "one",
        },
        {
          variantImage: "new.jpg",
          variantPrice: 200000,
          variantColor: "orange",
          variantTitle: "two",
        },
      ],
    };
  },
  methods: {
    updateProduct(index) {
      this.selectedVariant = index;
    },
  },
  computed: {
    title() {
      return this.variants[this.selectedVariant].variantTitle;
    },
    image() {
      return this.variants[this.selectedVariant].variantImage;
    },
    price() {
      return this.variants[this.selectedVariant].variantPrice;
    },
    credit() {
      return this.variants[this.selectedVariant].variantPrice / 12;
    },
  },
};
</script>

